.contact{
	padding: 40px 0;
	.contact-heading{
		h4{color: #2f2f2f; line-height: 30px; margin-bottom: 30px;}
		p{color: #797979; font-size: 15px; margin-bottom: 30px;}
	}
	.conatct-from{
		form{
			.contact-name ,.contact-email{
				width: 49%; display: inline-block;  margin-bottom: 30px;
				label{
					color: #797979; font-size: 15px; display: block; margin-bottom: 20px;
					span.required{color: red;}
				}
				input{width: 100%; height: 48px; border-radius: 30px;  border: 1px solid #eee;}
			}
			.contact-Message{
				width: 100%;
				label{
					color: #797979; font-size: 15px; display: block; margin-bottom: 20px;
					span.required{color: red;}
				}
				textarea{width: 100%; border-radius: 10px;  border: 1px solid #eee;}
			}
			.custom-btn{padding: 15px 40px; background-color: $yellow; border: 1px solid $white; color: $white; margin-top: 30px;}
		}
	}
	.contact-detail{
		.contact-info{
			h5{
				margin-top: 15px;margin-bottom: 20px; color: #2f2f2f;
			}
			ul{
				li{
					line-height: 30px; border-bottom: 1px solid #eee; padding: 10px 0;
					i{color: $blue; }
					a{padding-left: 15px;}
				}
			}
		}
	}
	
}