footer{
  background-image: url('../images/footer-bg.jpg'); background-size: cover; background-position: center center; 
  padding: 60px 0;
  .footer-main{
    img{max-width: 100%;}
    p{margin: 30px 0; color: #627279; text-align: left;}
    .social-icon{
      li{
        display: inline-block; margin-right: 10px;
        a{
          color: #7c7c7c; transition: all 0.5s easein;
          i{font-size: px-to-rem(16)}
          &:hover{
            color: #11adc6; transition: all 0.5s easein;
          }
        }
      }
    }
  }
  .solution-parent{
    padding: 0 30px;
    .heading-text{
      h3{
        color: $white;
        &:after{background-color: $blue;  height: 3px;}
      }
    }
    ul{
      padding: 0;
      li{
        a{
          position: relative; color: #627279; text-transform: capitalize; line-height: 40px; transition: all 0.5s ease; padding-left: 10px;
          &:hover{color: #009bb3;transition: all 0.5s ease;}
          // &:before{content: '\f068'; font-family: FontAwesome;}
        }
      }
    }
  }
}