.top-bar{
	background-color: $grey; 
	.top-info{
		display: flex; justify-content: space-between; padding: 10px; align-items: center;
		.industrial-info{
			h6{font-size: px-to-rem(16); color: #6e6e6e; font-family: $heading;}
		}
		.industrial-certicate{
			h6{font-size: px-to-rem(16);color: #6e6e6e;font-family: $heading;}
		}
	}
	
}
.logo-bar{
	padding: 20px 0;
	.top-logo{
		display: flex; align-items: center;
		.logo{
			width: 40%;
			img{max-width: 100%;}
		}
	}
	.company-contact{
		width: 30%;
		.company-address{
			display: flex; align-items: center; justify-content: center;
			.company-lc-icon{
				padding-right: 10px;
				i{}
			}
			.company-lc-info{
				h6{}
				span{}
			}
		}
	}
	.company-call{
		width: 30%;
		.company-phone{
			display: flex; align-items: center; justify-content: center;
			.company-ph-icon{
				padding-right: 10px;
				i{}
			}
			.company-ph-no{
				a{display: block;}
			}
		}
	}
}
header{
	background-color: $violet; padding: 15px 0;
	ul{
		display: flex;
		li{
			margin-right: 50px;
			a{
				color: $white; font-size: px-to-rem(22); font-weight: 500; font-family: $heading-nav; text-transform: uppercase; letter-spacing: 1.5px;
			}
		}
	}
}