.home-pagesilder{
	ul{
		li{
			img{max-width: 100%; width: 100%;}
		}
	}
}
.offer-section{
	padding: 50px 0; background-color: #f5f4f4;
	.offer-parent{
		display: flex;
		.offer-heading{
			width: 40%; position: relative; padding-right: 30px;
			h2{
				color: $dark; text-align: right; font-size: px-to-rem(28);  font-weight: 600; line-height: 1.3; letter-spacing: 0.5px;
				span{display: block;}
			}
			&:after{}
		}
		.offer-content{
			width: 60%; padding-left: 30px; border-left: 3px solid #25639a;
			p{color: #6c6c6c; font-weight: 300;}
		}
	}
}

.grid-process{
	background-color: #f5f4f4;
	.parent-process{
		margin-bottom: 30px;
		.process-img{
			a{
				img{max-width: 100%;}
			}
		}
		.process-content{
			a{
				h4{color: $dark; border-bottom: 2px solid #19a3b8; font-size: px-to-rem(22); margin: 20px 0; padding-bottom: 10px; text-transform: capitalize;}
			}
			p{color: #898989; font-size: px-to-rem(16)}
			a.readmore{
				color: $blue; font-size: px-to-rem(14); font-weight: 600; transition: all 0.5s ease; cursor: pointer; color: #0fa2d9;
				&:hover{transition: all 0.5s ease-in; color: $light-grey;}
			}
		}
	}
}

.quality-control{
	padding: 50px 0;
	.parent-quality{
		h2{color: $dark; font-size: px-to-rem(30); line-height: 1.4; text-align: center; margin-bottom: 30px;}
		p{text-align: center; color: #6c6c6c;}
	}
	.quality-icons{
		margin-top: 50px;
		ul{
			display: flex; justify-content:center;
			li{
				width: 15%; margin-right: 30px; border-right: 1px solid $blue; text-align: center;
				img{max-width: 100%; margin-bottom: 10px;}
				p{color: #6c6c6c; font-size: px-to-rem(20); font-weight: 600;}
				&:last-child{border-right: 0;}
			}
		}
	}
}

.industrial-img{
	padding: 80px 0 0s;
	.parent-industrial{
		display: table;
		.indust-left{
			display: table-cell; width: 50%;
			img{max-width: 100%;transition: opacity 0.35s, transform 0.35s; display: block;}
		}
		.indust-right{
			display: table-cell; background-image: url('../images/ser-img-left.jpg'); background-repeat: repeat; position: relative; width: 50%; vertical-align: middle;
			.slider-test{
				max-width: 670px; margin: 0; z-index: 2; position: relative; padding: 36px 10px 0px 130px;
				.slider-quote{

				}
				.slider-content{
					.media{
						img{max-width: 100%;}
						.media-body{
							margin-bottom: 20px;
							h5{color: $white; font-size: px-to-rem(20); margin-bottom: 10px;}
							span{color: $yellow; font-weight: 600; letter-spacing: 0.50px;}
						}
					}
					p{font-size: px-to-rem(16); color: $white; line-height: 1.5;}
				}
			}
			&:after{content: ''; position:absolute; height: 100%; width: 100%;top: 0; left: 0 ; right: 0;}
		}
	}
}

.looking-contact{
	background-color: $yellow; padding: 30px 0;
	.contact-content{
		display: flex; justify-content: space-around; align-items: center;
		.content-heading{
			h6{color: $white; font-size: px-to-rem(26); letter-spacing: 0.5px;}
		}
		.contact.button{
			a{background-color: $black; padding: 15px 50px; color: $white; font-size: px-to-rem(20)}
		}
	}
}

.about-us-faqs{
	background-image: url('../images/news-bg.jpg'); background-size: cover; padding: 80px 0;
	.parent-wrapper{
		display: flex;
		.about-us{
			width: 50%;
			.about-content{
				p{
					margin-bottom: 30px;letter-spacing: 1px;
					&.dim-text{color: #898989;}
				}
				ul{
					li{
						width: 49%; display: inline-block; margin-bottom: 15px;
						a{
							position: relative; font-size: px-to-rem(18); color: $dark; padding-left: 20px;
							&:before{content: '\f0a9'; position: absolute; left: 0; top: 50%; font-family: FontAwesome; font-size: px-to-rem(14); color: $violet; transform: translateY(-50%);}
						}
					}
				}
			}
		}
		.faq{width: 50%;}
	}
}

.project-sild{
	background-image: url('../images/project-bg.jpg');background-position: center top; background-repeat: no-repeat; padding: 80px 0;
		.heading-text{
			h3{color: $white;}
		}
	.parent-slider{
			ul{
				li{
					margin: 10px;
					a{
						img{max-width: 100%;}
					}
				}
			}
	}
}

.latest-news{
	padding: 80px 0;
	.prent-latest{
		.latest-img{
			position: relative;
			img{max-width: 100%; width: 100%;}
		}
		.latest-content{
			margin-top: 20px;
			h4{font-size: px-to-rem(18); line-height: 1.5; color: $dark; margin-bottom: 10px;}
			p{color: #898989; line-height: 1.5; text-align: left;}
		}
		.latest-tag{
			background-color: $yellow; position: absolute; width: 50px; height: 50px; top: 50%; left: 20px; text-align: center; 
			h6{ 
				color: $white;margin-top: 10px; font-weight: 600; font-family: $heading-nav;
				span{display: block;}
			}
		}
	}
}

.slogo{
	background-image: url('../images/pro-sol-bg.jpg'); padding: 40px 0; background-repeat: repeat; background-size: cover; background-position: center top; 
	h6{
		line-height: 1.5;   margin: 0 auto; text-align: center; color: $white; font-size: px-to-rem(28); letter-spacing: 1px;
		
	}
}