.heading-text{
	margin-bottom: 30px;
	h3{
		color: $black; font-size: px-to-rem(25); letter-spacing: 0.5px; font-weight: 600; position: relative; padding-bottom: 20px; font-family: $heading-nav; margin-bottom: 30px;
		&:after{content: '';  position: absolute; bottom: 0; left: 0;  background-color: $yellow; height: 5px; width: 50px;}
	}
	
	
}



.text-color{color: $yellow;}
.custom-btn{padding: 10px 40px; border-radius: 30px;  background-color: red; color: $white; margin: 20px 0;}

.banner-img{
	background-image: url('../images/abtbg.jpg'); background-attachment: fixed; 
	background-repeat: no-repeat; height: 250px; padding: 0; position: relative;
	.banner-info{
		position: absolute;top: 50%; left: 0 ; right: 0; transform: translateY(-50%);
		h6{color: $white; line-height: 30px; font-size: 20px; text-align: center;}
		span{color: $white; display: block; text-align: center;}
	}
}

